.stats-types {
  grid-row: content-s/content-e;
  grid-column: types-s/types-e;
  height: 560px;
  width: 520px;
  overflow: hidden;

  &__title {
    font-weight: bold;
    font-size: 36px;
    height: 40px;
    margin-bottom: 20px;
  }

  &__items {
    display: grid;
    grid-template-columns: 1fr;
    grid-row-gap: 20px;
    margin-left: 10px;
    width: 520px;
    height: 560px;
    overflow-y: scroll;
    padding-right: 30px;
  }

  &__empty {
    height: 60px;
  }

  &-mobile {
    display: none;
  }
}

@media only screen and (max-width: 48em) {
  .stats-types {
    display: none;
  }

  .stats-types-mobile {
    display: block;

    &__title {
      font-size: 25px;
      font-weight: bold;
      margin-left: 10px;
    }

    &__items {
      height: 150px;
      overflow-y: scroll;
    }
  }
}
